body {
  min-width: 320px;
  overflow-x: hidden;
  font-family: 'MuseoSansCyrl-300', sans-serif;
  color: #5c5c5c;
}
h1,
h2 {
  font-size: 40px;
  line-height: 55px;
  color: #141414;
  font-family: 'MuseoSansCyrl-300', sans-serif;
}
h1 span,
h2 span {
  font-family: 'MuseoSansCyrl-900', sans-serif;
}
h3 {
  font-size: 30px;
  line-height: 36px;
  color: #141414;
  font-family: 'MuseoSansCyrl-900', sans-serif;
  letter-spacing: 0.01em;
}
h4 {
  font-size: 25px;
  line-height: 32px;
  color: #141414;
  font-family: 'MuseoSansCyrl-900', sans-serif;
  letter-spacing: 0.01em;
}
h6 {
  font-size: 16px;
  color: #fff;
  font-family: 'MuseoSansCyrl-300', sans-serif;
}
h6 span {
  font-family: 'MuseoSansCyrl-900', sans-serif;
}
p {
  font-size: 20px;
  line-height: 26px;
  font-family: 'MuseoSansCyrl-300', sans-serif;
  color: #5c5c5c;
}
a {
  text-decoration: none;
}
.content {
  width: 1170px;
  margin: 0 auto;
}
.content.content-relative {
  position: relative;
  z-index: 3;
}
img {
  max-width: 100%;
}
section {
  padding-top: 90px;
  padding-bottom: 90px;
}
.title {
  text-align: center;
  margin-bottom: 90px;
}
.text-blue {
  color: #0d3b87;
}
.hidden {
  display: none;
}
