/* Slider */
.slick-slider
{
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
        touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list
{
    position: relative;

    display: block;
    overflow: hidden;

    margin: 0;
    padding: 0;
}
.slick-list:focus
{
    outline: none;
}
.slick-list.dragging
{
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
    -webkit-transform: translate3d(0, 0, 0);
       -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
         -o-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.slick-track
{
    position: relative;
    top: 0;
    left: 0;

    display: block;
    margin-left: auto;
    margin-right: auto;
}
.slick-track:before,
.slick-track:after
{
    display: table;

    content: '';
}
.slick-track:after
{
    clear: both;
}
.slick-loading .slick-track
{
    visibility: hidden;
}

.slick-slide
{
    display: none;
    float: left;

    height: 100%;
    min-height: 1px;
}
[dir='rtl'] .slick-slide
{
    float: right;
}
.slick-slide img
{
    display: block;
}
.slick-slide.slick-loading img
{
    display: none;
}
.slick-slide.dragging img
{
    pointer-events: none;
}
.slick-initialized .slick-slide
{
    display: block;
}
.slick-loading .slick-slide
{
    visibility: hidden;
}
.slick-vertical .slick-slide
{
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}

.advantages {
  padding-bottom: 40px;
}
.advantages-image {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}
.advantages-items {
  display: flex;
  flex-wrap: wrap;
  width: calc(100% - 70px);
  margin: 0 auto;
}
.advantages-item {
  width: 50%;
  margin-bottom: 50px;
  display: flex;
}
.advantages-item__icon {
  width: 30px;
  margin-left: 10px;
  margin-right: 20px;
  margin-top: 5px;
}
.advantages-item__text {
  width: calc(100% - 60px);
}
.advantages-item__text h3 {
  margin-bottom: 20px;
}
.any {
  padding-top: 160px;
  padding-bottom: 0;
  overflow: hidden;
  background: url("../img/assets/home/bg.jpg") no-repeat center/cover;
}
.any-wrapper {
  position: relative;
  padding-bottom: 65px;
}
.any-image {
  position: absolute;
  left: 55%;
  bottom: -1px;
  width: 100%;
}
.any-left {
  width: 55%;
}
.any .title {
  text-align: left;
  margin-bottom: 30px;
}
.any .title h2 {
  font-family: 'MuseoSansCyrl-900', sans-serif;
}
.any .subtitle {
  margin-bottom: 80px;
}
.any .subtitle h4 {
  color: #5c5c5c;
  font-family: 'MuseoSansCyrl-500', sans-serif;
  line-height: 35px;
}
.any .tagline {
  margin-bottom: 30px;
}
.any .tagline span {
  line-height: 26px;
  color: #0d3b87;
  font-family: 'MuseoSansCyrl-500', sans-serif;
  font-size: 20px;
}
.any-phones {
  margin-bottom: 70px;
}
.any-phones a {
  display: block;
  font-size: 40px;
  font-family: 'MuseoSansCyrl-900', sans-serif;
  color: #0d3b87;
  margin-bottom: 25px;
}
.any-phones a:last-child {
  margin-bottom: 0;
}
.any-phones a span {
  color: #141414;
}
.any-button {
  margin-bottom: 45px;
}
.any-bottom {
  display: flex;
  justify-content: space-between;
}
.any-write {
  display: flex;
  align-items: center;
}
.any-write__icon {
  margin-right: 10px;
}
.any-write__span span {
  color: #14930b;
  font-size: 20px;
  line-height: 30px;
  text-decoration: underline;
}
.any-specialist span {
  color: #0e0e0e;
  display: block;
  font-size: 25px;
  line-height: 35px;
  font-family: 'MuseoSansCyrl-900', sans-serif;
}
.any-specialist span:last-child {
  margin-bottom: 0;
  color: #5c5c5c;
  font-size: 16px;
}
.button {
  height: 80px;
  display: inline-flex;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  box-sizing: border-box;
  border: 1px solid #b80000;
  box-sizing: border-box;
  background: #d10707;
  cursor: pointer;
  overflow: hidden;
  border-radius: 5px;
  position: relative;
  box-shadow: 0px -5px 0px 0px rgba(0,0,0,0.3) inset, 0px 5px 30px 0px rgba(211,18,18,0.4);
  transition: all 0.5s ease-in-out;
}
.button::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.28;
  background: linear-gradient(to bottom, #fff 0%, rgba(255,255,255,0) 100%);
}
.button span {
  position: relative;
  z-index: 3;
  font-size: 16px;
  color: #fff;
  font-family: 'MuseoSansCyrl-700', sans-serif;
  text-transform: uppercase;
}
.button.button-pdf {
  padding-left: 33px;
  padding-right: 80px;
}
.button.button-pdf::after {
  content: '';
  position: absolute;
  width: 30px;
  height: 34px;
  background: url("../img/assets/price/pdf.svg") no-repeat center/contain;
  right: 30px;
  top: 12px;
}
.button.button-pdf span {
  display: block;
  text-align: center;
}
.button.button-pdf span:last-child {
  color: #e59d9d;
  font-size: 12px;
  line-height: 30px;
  letter-spacing: 0.025em;
}
.button.quiz-button {
  cursor: pointer;
  padding-left: 40px;
  padding-right: 65px;
}
.button.quiz-button::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  top: 50%;
  margin-top: -10px;
  right: 30px;
  background: url("../img/assets/quiz/right.svg") no-repeat center/contain;
}
.button.quiz-button.quiz-button__prev {
  padding-right: 45px;
  padding-left: 60px;
  background: none;
  border: 1px solid #5c5c5c;
  box-shadow: none;
}
.button.quiz-button.quiz-button__prev span {
  color: #5c5c5c;
}
.button.quiz-button.quiz-button__prev::after {
  content: '';
  right: auto;
  left: 30px;
  background: url("../img/assets/quiz/left.svg") no-repeat center/contain;
}
.button.quiz-button.quiz-button__prev::before {
  content: none;
}
.contacts {
  position: relative;
  padding: 0;
  height: 500px;
}
.contacts #map {
  position: absolute;
  left: 0;
  z-index: 1;
  top: 0;
  width: 100%;
  height: 100%;
}
.contacts .content {
  height: 100%;
  position: relative;
}
.contacts-pop {
  position: absolute;
  right: 0;
  top: 50%;
  padding-left: 40px;
  padding-right: 30px;
  padding-top: 45px;
  background: #fff;
  padding-bottom: 45px;
  box-shadow: 0px 5px 30px 0px rgba(20,20,20,0.25);
  transform: translateY(-50%);
  width: 390px;
  box-sizing: border-box;
  z-index: 4;
  border-radius: 5px;
}
.contacts-pop span {
  font-family: 'MuseoSansCyrl-900', sans-serif;
  color: #141414;
  font-size: 30px;
}
.contract-items {
  display: flex;
}
.contract-item {
  width: 25%;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
}
.contract-item__photo {
  position: relative;
  width: 143px;
  margin: 0 auto;
  margin-bottom: 30px;
  height: 143px;
  background: #0d3b87;
  border-radius: 50%;
}
.contract-item__photo img {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -5px;
  max-width: unset;
  z-index: 1;
}
.contract-item__number {
  width: 60px;
  height: 60px;
  border-radius: 50%;
